.b-select a {
  color: #535353 !important;
  text-decoration: none; }

.selectBox-dropdown {
  display: block !important;
  width: 100% !important;
  padding-right: 46px !important;
  position: relative;
  border: solid 1px #c4c4c4;
  line-height: 1.375;
  text-decoration: none;
  text-align: left;
  color: #535353;
  outline: none;
  vertical-align: middle;
  background: transparent;
  border-radius: 3px;
  cursor: pointer;
  text-decoration: none !important; }

.selectBox-label {
  display: block;
  width: auto !important;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.selectBox-dropdown .selectBox-arrow {
  position: absolute;
  top: 0;
  right: 0;
  width: 46px;
  height: 40px; }
  .selectBox-dropdown .selectBox-arrow:before {
    content: '';
    position: absolute;
    top: 50%;
    margin-top: -2px;
    right: 18px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 5px 0 5px;
    border-color: #c4c4c4 transparent transparent transparent;
    transition: all 0.3s; }

.selectBox-dropdown-menu {
  position: absolute;
  z-index: 99999;
  max-height: 200px;
  border: solid 1px #c4c4c4;
  padding: 3px 0;
  margin: 0;
  background: #FFF;
  overflow: auto;
  -webkit-overflow-scrolling: touch; }

.selectBox-options a {
  display: block;
  width: 100%;
  padding: 8px 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1;
  color: #535353;
  text-decoration: none;
  cursor: pointer; }
  .selectBox-options a:hover, .selectBox-options a:focus {
    color: inherit;
    text-decoration: none;
    background-color: #f0f0f0; }

.selectBox-disabled a {
  color: #a1a1a1;
  cursor: default; }
  .selectBox-disabled a:hover, .selectBox-disabled a:focus {
    color: #a1a1a1;
    background-color: transparent; }

.b-tags {
